import "./setNewPassword.scss";
import { useNavigate, useParams } from "react-router-dom";
import { Button, TextField } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import React, { useState, useEffect } from "react";
import { clearMessage } from "../../../../redux/message";
import Link from "@material-ui/core/Link";
import { resetPassword } from "../../../../redux/authSlice";
const SetNewPassword = () => {
  const { user } = useSelector((state) => state.auth);
  const [successful, setSuccessful] = useState(false);
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [check, setCheck] = useState("");
  const { message } = useSelector((state) => state.message);
  const { capcha } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  useEffect(() => {
    dispatch(clearMessage());
    if (user) navigate("/buyerHome");
  }, [dispatch]);

  const handleSetNewPassword = (e) => {
    e.preventDefault();
    setCheck(true);
    setSuccessful(false);

    console.log({ capcha, password });
    if (
      !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*\s]{6,30}$/.test(
        password
      )
    ) {
    } else if (confirmPassword != password) {
    } else {
      dispatch(resetPassword({ capcha, password }))
        .unwrap()
        .then(() => {
          navigate("/auth/login", {
            state: { alert: "Tao mt khu mi thanh cng!" },
          });
          setSuccessful(true);
        })
        .catch(() => {
          setSuccessful(false);
        });
    }
  };
  return (
    <div className="login">
      <Link href="/">
        <p className="logo_login">Jovinn.</p>
      </Link>

      <form className="form" onSubmit={handleSetNewPassword}>
        <p className="title_form">Thit lp mt khu</p>
        <TextField
          className="input"
          variant="outlined"
          type="password"
          label="Mt khu mi"
          onChange={(e) => setPassword(e.target.value)}
          error={
            !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*\s]{6,30}$/.test(
              password
            ) && check
          }
          helperText={
            !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*\s]{6,30}$/.test(
              password
            ) &&
            check &&
            "T 6 n 30 ki t, it nht 1 ki t vit hoa va s"
          }
          required
        />
        <TextField
          className="input"
          variant="outlined"
          type="password"
          label="Xac nhn mt khu"
          onChange={(e) => setConfirmPassword(e.target.value)}
          error={confirmPassword != password && check}
          helperText={
            confirmPassword != password &&
            check &&
            "Mt khu xac nhn phai ging vi mt khu"
          }
          required
        />
        {/* <span className="link">It nht 1 ki t vit thng</span>
        <span className="link">It nht 1 ki t vit hoa</span>
        <span className="link">8 n 16 ki t</span>
        <span className="link">
          Chi ch cai , s va ki t ph bin thng thng
        </span> */}
        <Button
          variant="outlined"
          className="btn"
          type="submit"
          style={{ marginTop: "15px" }}
        >
          Xac nhn
        </Button>
        <span className="link">
          <Link href="/auth/login" color="#5327ef">
             Quay v ng nhp
          </Link>
        </span>
        {message && (
          <div
            className={successful ? "login_success" : "login_error"}
            role="alert"
          >
            {message}
          </div>
        )}
        {successful && (
          <div
            className={successful ? "login_success" : "login_error"}
            role="alert"
          >
            Tao mt khu thanh cng!
          </div>
        )}
      </form>
    </div>
  );
};

export default SetNewPassword;
