import {
  AppBar,
  Button,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  MenuItem,
  TextField,
  makeStyles,
  Toolbar,
  Typography,
  List,
  ListItem,
  InputAdornment,
  ListItemText,
  ListItemAvatar,
  Avatar,
  FormControl,
  LinearProgress,
} from "@material-ui/core";
import { Close, CloudUpload, AddSharp, RemoveSharp } from "@material-ui/icons";
import Alert from "@material-ui/lab/Alert";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import BuyerHeader from "../../../components/buyer/buyerHeader/BuyerHeader";
import Contact from "../../../components/guest/contact/Contact";
import { selectAllCategories } from "../../../redux/categorySlice";
import { addRequest, fetchRequestsBuyer } from "../../../redux/requestSlice";

import {
  selectCurrentUser,
  selectTopSellers,
  uploadFile,
} from "../../../redux/userSlice";
import "./buyerCreateRequest.scss";

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

export default function BuyerCreateRequest() {
  const topSeller = useSelector(selectTopSellers);
  const listCategory = useSelector(selectAllCategories);
  const [cateId, setCateId] = useState(listCategory[0].id);
  const [subCateId, setSubCateId] = useState("");
  const [recruitLevel, setRecruitLevel] = useState("BEGINNER");
  const [jobTitle, setJobTitle] = useState("");
  const [description, setDescription] = useState("");
  const [skills, setSkills] = useState([]);
  const [inviteUsers, setInviteUsers] = useState([]);
  const [stages, setStages] = useState([
    { startDate: "", endDate: "", description: "", milestoneFee: "0.00" },
  ]);
  const [cancleFee, setCancleFee] = useState(0);
  const request = {
    categoryId: cateId,
    subCategoryId: subCateId,
    recruitLevel: recruitLevel,
    skillsName: skills,
    jobTitle: jobTitle,
    shortRequirement: description,
    milestoneContracts: stages,
    contractCancelFee: cancleFee,
    invitedUsers: inviteUsers,
    attachFile: file,
  };
  const dispatch = useDispatch();
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [check, setCheck] = useState(false);
  const maxDate = new Date();
  maxDate.setHours(0, 0, 0, 0);
  maxDate.setDate(maxDate.getDate());
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const currentUser = useSelector(selectCurrentUser);
  const handleUploadFile = async (e) => {
    setLoading(true);
    setFile(e.target.files[0]);
    console.log(e.target.files[0].name);
    const formData = new FormData();
    formData.append("file", e.target.files[0]);
    formData.append("id", currentUser.id);
    formData.append("type", "BOX");

    dispatch(uploadFile(formData))
      .unwrap()
      .then(() => {
        setLoading(false);
        // toast.success("Anh 1 tai ln thanh cng");
      })
      .catch(() => {
        setLoading(false);
      });
  };
  // ssssssss
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setError("");
    let check1 = false;
    let check3 = true;
    setCheck(true);
    stages.map((stage, index) => {
      if (
        new Date(stage.startDate) < maxDate ||
        stage.startDate > stage.endDate ||
        !stage.startDate
      ) {
        check3 = false;
        setError("Cha nhp ngay bt u cua giai oan " + parseInt(index + 1));
      } else if (
        new Date(stage.endDate) < maxDate ||
        stage.startDate > stage.endDate ||
        !stage.endDate
      ) {
        check3 = false;
        setError(
          "Cha nhp ngay kt thuc cua giai oan " + parseInt(index + 1)
        );
      } else if (
        !/^[^\s][a-zA-Z\s\W|_]{28,498}[^\s]$/.test(
          stage.description
        )
      ) {
        check3 = false;
        setError(
          "Cha nhp san phm ban giao cua giai oan " + parseInt(index + 1)
        );
      } else if (
        stage.milestoneFee < 1 ||
        stage.milestoneFee.length > 10 ||
        stage.milestoneFee == ""
      ) {
        check3 = false;
        setError("Cha nhp chi phi cua giai oan " + parseInt(index + 1));
      }
    });
    if (
      subCateId == "" ||
      !/^[^\s][a-zA-Z\s\W|_]{3,48}[^\s]$/.test(
        jobTitle
      ) ||
      !/^[^\s][a-zA-Z\s\W|_]{28,498}[^\s]$/.test(
        description
      ) ||
      cancleFee < 0 ||
      cancleFee > 100 ||
      !cancleFee ||
      skills.length == 0
    ) {
      setError("Cha nhp ki nng!");
    } else {
      check1 = true;
    }

    if (check1 && check3) {
      setOpen(true);
      setCheck(false);
    }
  };

  const handleClose = (e) => {
    e.preventDefault();
    dispatch(addRequest(request))
      .unwrap()
      .then(() => {
        dispatch(fetchRequestsBuyer());
        setSuccess("Tao yu cu thanh cng!");
        navigate("/buyerHome/manageRequest", {
          state: {
            alert: "Tao yu cu thanh cng",
          },
        });
      })
      .catch(() => {
        setError("Tao yu cu tht bai!");
      });
    setOpen(false);
  };

  const [fullScreenOpen, setFullScreenOpen] = useState(false);
  const handleFullScreenOpen = () => {
    setFullScreenOpen(true);
  };
  const navigate = useNavigate();
  const handleFullScreenClose = (e) => {
    e.preventDefault();
    dispatch(addRequest(request))
      .unwrap()
      .then(() => {
        dispatch(fetchRequestsBuyer());
        setSuccess("Tao yu cu thanh cng!");
        navigate("/buyerHome/manageRequest", {
          state: {
            alert: "Tao yu cu thanh cng",
          },
        });
      })
      .catch(() => {
        setError("Tao yu cu tht bai!");
      });
    setFullScreenOpen(false);
    setOpen(false);
  };

  const handleStageChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...stages];
    // if (name == "milestoneFee") {
    //   list[index][name] = parseFloat(value).toFixed(2);
    // } else {
    //   list[index][name] = value;
    // }
    list[index][name] = value;
    setStages(list);
  };

  const handleStageAdd = () => {
    setStages([
      ...stages,
      { startDate: "", endDate: "", description: "", milestoneFee: "0.00" },
    ]);
  };

  const handleStageRemove = () => {
    if (stages.length > 1) {
      const list = [...stages];
      list.pop();
      setStages(list);
    }
  };

  function handleKeyDown(e) {
    if (e.key !== "Enter") return;
    const value = e.target.value;
    if (!value.trim()) return;
    setSkills([...skills, value]);
    e.target.value = "";
  }

  function removeSkill(index) {
    setSkills(skills.filter((el, i) => i !== index));
  }

  return (
    <div className="buyer_profile">
      <BuyerHeader />
      <h1 className="buyer_profile_title">Tao yu cu</h1>
      <Container maxWidth="lg" className="profession_form">
        {" "}
        <div className="profession_row">
          <TextField
            id="outlined-select-currency"
            select
            label="Chon danh muc"
            value={cateId}
            onChange={(e) => setCateId(e.target.value)}
            style={{ width: "30%", margin: "10px" }}
            variant="outlined"
          >
            {listCategory.map((category, index) => (
              <MenuItem key={index} value={category.id}>
                {category.name}
              </MenuItem>
            ))}
          </TextField>
          <TextField
            id="outlined-select-currency"
            select
            label="Chon danh muc con"
            value={subCateId}
            onChange={(e) => setSubCateId(e.target.value)}
            style={{ width: "30%", margin: "10px" }}
            variant="outlined"
            error={!subCateId && check}
            helperText={!subCateId && check && "Cha chon danh muc con!"}
          >
            {listCategory
              .find((val) => {
                return val.id == cateId;
              })
              .subCategories.map((subCategory, index) => (
                <MenuItem key={index} value={subCategory.id}>
                  {subCategory.name}
                </MenuItem>
              ))}
          </TextField>
        </div>
        <div className="profession_row">
          <TextField
            id="outlined-select-currency"
            select
            label="Trinh  ngi ban"
            defaultValue="BEGINNER"
            name="level"
            onChange={(e) => setRecruitLevel(e.target.value)}
            style={{ width: "23%", margin: "10px" }}
            variant="outlined"
          >
            <MenuItem value="BEGINNER">BEGINNER</MenuItem>
            <MenuItem value="ADVANCED">ADVANCED</MenuItem>
            <MenuItem value="COMPETENT">COMPETENT</MenuItem>
            <MenuItem value="PROFICIENT">PROFICIENT</MenuItem>
            <MenuItem value="EXPERT">EXPERT</MenuItem>
          </TextField>
          <div className="tags-input-container">
            {skills.map((skill, index) => (
              <div className="tag-item" key={index}>
                <span className="text">{skill}</span>

                <span className="close" onClick={() => removeSkill(index)}>
                  &times;
                </span>
              </div>
            ))}
            <input
              onKeyDown={handleKeyDown}
              type="text"
              className="tags-input"
              placeholder="Nhp ki nng"
            />
          </div>
        </div>
        <div className="profession_row">
          <TextField
            id="outlined-basic"
            label="Tiu "
            variant="outlined"
            style={{ width: "62%" }}
            onChange={(e) => setJobTitle(e.target.value)}
            error={
              !/^[^\s][a-zA-Z0-9\s\W|_]{3,48}[^\s]$/.test(
                jobTitle
              ) && check
            }
            helperText={
              !/^[^\s][a-zA-Z0-9\s\W|_]{3,48}[^\s]$/.test(
                jobTitle
              ) &&
              check &&
              "T 5 n 50 ki t khng c bt u vi khoang trng"
            }
          />
        </div>
        <div className="profession_row">
          <TextField
            id="outlined-basic"
            label="M ta"
            variant="outlined"
            multiline
            rows={6}
            style={{ width: "62%" }}
            onChange={(e) => setDescription(e.target.value)}
            error={
              !/^[^\s][a-zA-Z0-9\s\W|_]{28,498}[^\s]$/.test(
                description
              ) && check
            }
            helperText={
              !/^[^\s][a-zA-Z0-9\s\W|_]{28,498}[^\s]$/.test(
                description
              ) &&
              check &&
              "T 30 n 500 ki t khng c bt u vi khoang trng"
            }
          />
        </div>
        <div className="profession_row">
          {" "}
          <FormControl className="request_form_control">
            <input
              accept="image/*,.doc,.docx,.xlsx,.xls,.csv,.pdf,text/plain"
              className="request_form_input"
              id="request-input-file"
              multiple
              type="file"
              onChange={handleUploadFile}
              hidden
            />
            <label htmlFor="request-input-file">
              <Button
                variant="contained"
                color="primary"
                component="span"
                startIcon={<CloudUpload />}
              >
                {file ? file.name : "FILE NH KM"}
              </Button>
            </label>{" "}
            {loading && <LinearProgress />}
          </FormControl>
        </div>
        <div className="profession_row">
          {" "}
          <Button style={{ height: "70px" }} onClick={handleStageRemove}>
            <RemoveSharp />
          </Button>
          <TextField
            id="outlined-basic"
            label="S giai oan"
            variant="outlined"
            type="number"
            value={stages.length}
            style={{ width: "8%", margin: "10px" }}
            disabled
          />
          <Button style={{ height: "70px" }} onClick={handleStageAdd}>
            <AddSharp />
          </Button>
        </div>
        {stages.map((stage, index) => (
          <div className="profession_itemStage" key={index}>
            <div className="profession_row">
              {stages.length > 1 && <h3>Giai oan {index + 1}</h3>}
            </div>
            <div className="profession_row">
              <TextField
                id="outlined-basic"
                label="Ngay bt u"
                variant="outlined"
                type="date"
                InputLabelProps={{
                  shrink: true,
                }}
                style={{ width: "30%", margin: "10px" }}
                name="startDate"
                onChange={(e) => handleStageChange(e, index)}
                error={
                  (new Date(stage.startDate) < maxDate ||
                    stage.startDate > stage.endDate ||
                    !stage.startDate) &&
                  check
                }
                helperText={
                  (new Date(stage.startDate) < maxDate ||
                    stage.startDate > stage.endDate ||
                    !stage.startDate) &&
                  check &&
                  "Phai t ngay hin tai tr i va trc ngay kt thuc"
                }
              />
              <TextField
                id="outlined-basic"
                label="Ngay kt thuc"
                variant="outlined"
                type="date"
                InputLabelProps={{
                  shrink: true,
                }}
                style={{ width: "30%", margin: "10px" }}
                name="endDate"
                onChange={(e) => handleStageChange(e, index)}
                error={
                  (new Date(stage.endDate) < maxDate ||
                    stage.startDate > stage.endDate ||
                    !stage.endDate) &&
                  check
                }
                helperText={
                  (new Date(stage.endDate) < maxDate ||
                    stage.startDate > stage.endDate ||
                    !stage.endDate) &&
                  check &&
                  "Phai t ngay hin tai tr i va sau ngay bt au"
                }
              />
            </div>
            <div className="profession_row">
              {" "}
              <TextField
                id="outlined-basic"
                label="San phm ban giao"
                variant="outlined"
                multiline
                rows={3}
                style={{ width: "62%" }}
                name="description"
                onChange={(e) => handleStageChange(e, index)}
                error={
                  !/^[^\s][a-zA-Z0-9\s\W|_]{28,498}[^\s]$/.test(
                    stage.description
                  ) && check
                }
                helperText={
                  !/^[^\s][a-zA-Z0-9\s\W|_]{28,498}[^\s]$/.test(
                    stage.description
                  ) &&
                  check &&
                  "T 30 n 500 ki t khng c bt u vi khoang trng"
                }
              />
            </div>
            <div className="profession_row">
              {" "}
              <TextField
                id="outlined-basic"
                label="Chi phi"
                variant="outlined"
                type="number"
                // value={stage.milestoneFee}
                style={{ width: "30%", margin: "10px" }}
                inputProps={{
                  maxLength: 10,
                  step: "0.01",
                }}
                // InputProps={{
                //   endAdornment: (
                //     <InputAdornment position="end">$</InputAdornment>
                //   ),
                // }}
                name="milestoneFee"
                onChange={(e) => handleStageChange(e, index)}
                error={
                  (stage.milestoneFee < 1 ||
                    stage.milestoneFee.length > 10 ||
                    stage.milestoneFee == "") &&
                  check
                }
                helperText={
                  (stage.milestoneFee < 1 ||
                    stage.milestoneFee.length > 10 ||
                    stage.milestoneFee == "") &&
                  check &&
                  "Ti thiu la 1$ , ti a 10 ch s"
                }
              />
            </div>
          </div>
        ))}
        <div className="profession_row">
          <Typography variant="h4">
            Tng chi phi :{" "}
            {stages.reduce(
              (total, item) => total + parseInt(item.milestoneFee),
              0
            )}{" "}
            $
          </Typography>
          <TextField
            id="outlined-basic"
            label="Phi huy hp ng"
            variant="outlined"
            type="number"
            style={{ width: "30%", margin: "10px" }}
            inputProps={{ min: 0 }}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  % Tng chi phi (={" "}
                  {(stages.reduce(
                    (total, item) => total + parseFloat(item.milestoneFee),
                    0
                  ) *
                    cancleFee) /
                    100}
                  $)
                </InputAdornment>
              ),
            }}
            onChange={(e) => setCancleFee(e.target.value)}
            error={(cancleFee < 0 || cancleFee > 100 || !cancleFee) && check}
            helperText={
              (cancleFee < 0 || cancleFee > 100 || !cancleFee) &&
              check &&
              "Ti thiu la 0% , ti a la 100%"
            }
          />
        </div>
        <div className="profession_row">
          {" "}
          <Button
            variant="contained"
            color="primary"
            className="form_right_row_btn"
            onClick={handleOpen}
          >
            Gi yu cu
          </Button>
        </div>
        {error !== "" && <Alert severity="error">{error}</Alert>}
        {success !== "" && <Alert severity="success">{success}</Alert>}
        <Dialog open={open} onClose={handleClose}>
          <DialogTitle id="dialod-title">
            {"Bn c mun gi li n ngi bn khng?"}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              Yu cu  c to thnh cng!Hy gi li mi n nhng ngi bn
              tim nng chng ti tm c.
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Khng</Button>
            <Button onClick={handleFullScreenOpen} color="primary">
              C
            </Button>
            <Dialog
              fullScreen
              open={fullScreenOpen}
              onClose={handleFullScreenClose}
            >
              <AppBar className={classes.appBar}>
                <Toolbar>
                  <IconButton
                    edge="start"
                    color="inherit"
                    onClick={handleFullScreenClose}
                    aria-label="close"
                  >
                    <Close />
                  </IconButton>
                  <Typography variant="h6" className={classes.title}>
                    Ngi bn tim nng
                  </Typography>
                  <Button color="inherit" onClick={handleFullScreenClose}>
                    Hon thnh
                  </Button>
                </Toolbar>
              </AppBar>
              <List
                style={{
                  width: "50%",
                  margin: "0 auto",
                  border: " 2px solid rgb(238, 225, 225)",
                }}
              >
                {topSeller.map((item, index) => {
                  return (
                    <ListItem button key={index}>
                      <ListItemAvatar>
                        <Avatar alt="buyer image" src={item.user.avatar} />
                      </ListItemAvatar>
                      <ListItemText
                        primary={item.user.firstName + " " + item.user.lastName}
                        secondary={item.skills.map((skill) => skill.name)}
                      />

                      {inviteUsers.find((i) => i.id === item.user.id) ? (
                        <Button
                          variant="outlined"
                          color="default"
                          onClick={() =>
                            setInviteUsers(
                              inviteUsers.filter((el) => el.id !== item.user.id)
                            )
                          }
                        >
                          Hoan tac
                        </Button>
                      ) : (
                        <Button
                          variant="outlined"
                          color="secondary"
                          onClick={() =>
                            setInviteUsers([
                              ...inviteUsers,
                              { id: item.user.id },
                            ])
                          }
                        >
                          Mi
                        </Button>
                      )}
                    </ListItem>
                  );
                })}
              </List>
            </Dialog>
          </DialogActions>
        </Dialog>
      </Container>
      <div className="sections_profile">
        <Contact />
      </div>
    </div>
  );
}
