import "./login.scss";
import { useLocation, useNavigate } from "react-router-dom";
import { Button, TextField } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import React, { useState, useEffect } from "react";
import { clearMessage } from "../../../redux/message";
import { login } from "../../../redux/authSlice";
import Link from "@material-ui/core/Link";
import { fetchCurrentUser } from "../../../redux/userSlice";
import { toast, ToastContainer } from "react-toastify";
const Login = () => {
  const { user } = useSelector((state) => state.auth);
  const [successful, setSuccessful] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [check, setCheck] = useState(false);
  const { message } = useSelector((state) => state.message);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { state } = useLocation();
  const { alert } = state || {};
  useEffect(() => {
    dispatch(clearMessage());
    if (user) navigate("/buyerHome");
  }, [dispatch]);

  const handleLogin = (e) => {
    e.preventDefault();

    console.log("user name password: ", { username, password });
    if (
      (!(
        /^[a-zA-Z0-9]{6,30}$/.test(username) ||
        /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(username)
      ) &&
        check) ||
      password.length < 6 ||
      password.length > 30
    ) {
      setCheck(true);
    } else {
      dispatch(login({ username, password }))
        .unwrap()
        .then(() => {
          setSuccessful(true);
          dispatch(fetchCurrentUser());
        })
        .then(() => {
          navigate("/buyerhome");
        })
        .catch(() => {
          setSuccessful(false);
        });
    }
  };
  useEffect(() => {
    if (alert) {
      toast.success(alert);
    }
  }, []);
  return (
    <div className="login">
      <ToastContainer limit={5000} position="bottom-right" />
      <Link href="/">
        <p className="logo_login">Jovinn.</p>
      </Link>

      <form className="form" onSubmit={handleLogin}>
        <p className="title_form">ng nhp</p>
        <TextField
          className="input"
          variant="outlined"
          label="Tn ng nhp/Email"
          onChange={(e) => setUsername(e.target.value)}
          error={
            !(
              /^[a-zA-Z0-9]{6,30}$/.test(username) ||
              /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(username)
            ) && check
          }
          helperText={
            !(
              /^[a-zA-Z0-9]{6,30}$/.test(username) ||
              /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(username)
            ) &&
            check &&
            "T 6 n 30 ki t"
          }
          required
        />
        <TextField
          className="input"
          variant="outlined"
          type="password"
          label="Mt khu"
          onChange={(e) => setPassword(e.target.value)}
          error={(password.length < 6 || password.length > 30) && check}
          helperText={
            (password.length < 6 || password.length > 30) &&
            check &&
            "T 6 n 30 ki t"
          }
          required
        />

        <Button variant="outlined" className="btn" type="submit">
          ng nhp
        </Button>
        <Link href="/auth/sendMail" color="#5327ef" className="link">
          Qun mt khu?
        </Link>
        <span className="link">
          Cha co tai khoan?{" "}
          <Link href="/auth/register" color="#5327ef">
            ng ki
          </Link>
        </span>

        {message && (
          <div
            className={successful ? "login_success" : "login_error"}
            role="alert"
          >
            {message}
          </div>
        )}
      </form>
    </div>
  );
};

export default Login;
